@import url(https://fonts.googleapis.com/css?family=Poppins:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  padding-top: 10px;
}

body {
  background-image: url(/static/media/Moon_Santa_STA.793e5ebc.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.space-between-header {
  padding-top: 20px;
  justify-content: center;
}

.App-main {
  -webkit-filter: blur(10px);
          filter: blur(10px);
  pointer-events: none;
}

.hiddenPete {
  visibility: hidden;
  position: absolute;
  height: 255px;
  z-index: -1;
  bottom: 0px;
  right: 0px;
}

.calendar-window {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 20vh;
  width: 15vh;
  padding: 0;
  color: #ffffff;
}

.calendar-window img {
  height: 15vh;
  width: 15vh;
  padding-bottom: 10px;
}

.calendar-window b {
  font-size: 2rem;
  color: white;
  height: 3rem;
  width: 3rem;
  background-color: rgba(60, 60, 59, 0.75);
  border-radius: 50%;
}

.activity-page-container,
.awesome-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

.activity-row,
.awesome {
  background-color: rgba(60, 60, 59, 0.75);
  padding: 10px 0;
  height: auto;
  margin: 0;
  border-radius: 5px;
}

.activity-row {
  width: 50vw;
}

.awesome {
  width: 70vw;
  height: 70vh;
}

Button.btn.btn-primary {
  margin: 10px auto;
  background-color: rgba(60, 60, 59, 0.75);
  border-color: white;
}

.wall-of-awesome-image {
  height: 50vh;
  width: 50vw;
}

.carousel-caption {
  background-color: rgba(60, 60, 59, 0.75);
}

.carousel-caption h2,
.carousel-caption h3 {
  margin: 0;
  padding: 0;
}

.challenge-text {
  padding-top: 20px;
  background-color: #f2f2f2;
  margin: 0;
  color: #3C3C3B;
}

.challenge-text p {
  padding-top: 20px;
  color: #3C3C3B;
}

.challenge-img {
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 1300px) {
  .activity-row {
    width: 80vw;
  }
}

@media screen and (max-width: 1150px) {
  .activity-row {
    width: 100vw;
  }
}

@media screen and (max-width: 1024px) {
  .calendar-window {
