    font-size: 0.5rem !important;
    font-weight: bold;
  }
  #logo {
    height: 4rem !important;
  }
  .App header h1 {
    font-size: 1rem !important;
  }
  .App header h2 {
    font-size: 0.75rem  !important;
  }
  .middle-col {
    padding: 0;
  }
}

@media screen and (max-width: 1000px) {
  .wall-of-awesome-image {
    height: 50vh;
    width: 100vw;
  }
  .awesome {
    height: 65vh;
  }
  .woa-carousel h1 {
    font-size: 1rem;
  }
  .woa-carousel h2 {
    font-size: 0.75rem;
  }
}

@media screen and (max-width: 320px) {
  #logo {
    height: 3rem;
  }
  .App header h1 {
    font-size: 0.75rem !important;
  }
  .App header h2 {
    font-size: 0.5rem  !important;
  }
  Button.btn.btn-primary {
