.loading-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%);
}

.loading-text-off {
  display: block;
}

@keyframes loadingTextFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.video-player {
  margin: 20px auto;
  opacity: 0;
}

.video-instructions {
  text-align: left;
  width: 80%;
  font-size: 1rem;
  margin: auto;
}

@keyframes videoFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: 1024px) {
  .video-player {
    width: 80vw !important;
    height: 40vh !important;
